import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NavController } from '@ionic/angular';

@Component({
  selector: 'app-parametre',
  templateUrl: './parametre.page.html',
  styleUrls: ['./parametre.page.scss'],
})
export class ParametrePage implements OnInit {

  public routerName= '';
  public pageInfo = {};

  constructor(private router: Router, private route: ActivatedRoute,private navCtrl: NavController) { }
   // eslint-disable-next-line @typescript-eslint/member-ordering

  ngOnInit() {
    const paramName: string =  this.route.snapshot.paramMap.get('name');
    this.routerName = paramName ;
    this.pageInfo = {title: 'Paramètre', subTitle: this.routerName };
    // console.log(this.routerName);
  }

  redirect() {
    this.navCtrl.back();
  }



}
